body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* Module Wrapper - Top leevel main  Wrapper*/
.layout__moduleWrapper___FrTl {
    width:650px;
    margin:0 auto;
    padding: 20px;
}
/* Wrap the all content in Module moduleContainer */
.layout__moduleContainer__1vx6n{
    background-color:#f1f1f1;
    border-radius: 5px;
    min-height:400px;
    box-shadow: 0 8px 6px -6px black;
}
/* Styles for Header Container */
.layout__HeaderContainer__3NQ4_{
    background-color: rgb(255, 90, 97);
    text-align: center;
    color: #fff;
    padding:10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
/* Styles for Content*/
.layout__contentContainer__2f9wH{
    padding: 30px;
}

/* On screens that are 768px or less, set the Module Wrapper width 100% */
@media screen and (max-width: 768px) {
    .layout__moduleWrapper___FrTl {
        width:100%;
        padding: 0px;
        margin:0px;
    }
    .layout__moduleContainer__1vx6n{
        margin:10px;
    }
    .layout__HeaderContainer__3NQ4_{
       
        padding:5px;
        
    }
    .layout__contentContainer__2f9wH{
        padding: 10px;
    }
}
.intro__submitButton__IPjwm{
    display:block;
    margin-top:20px;
    background-color: #0da9de;
    color: #fff !important;
    padding: 8px 22px;
    text-transform: uppercase;
    text-align: center;
    border: none;
    font-weight: 500;
    font-size: 16px;
    cursor:pointer;
}

.intro__instructionText__2OMiS{
    font-size: 12px;
    color:#ff5a61;

}
.table__rentTable__gTEul table{
    width:100%;
    border-collapse: collapse;
}

.table__rentTable__gTEul th, .table__rentTable__gTEul td{
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}
